import { Link } from 'react-router-dom';
import logo from '../Assests/Images/flat-design-clothing-store-logo-design_23-2149496415.jpg';
import useDarkMode from '../Utils/Common';



const Navbar = (props) => {

    const [mode, toggleMode] = useDarkMode();

    

    return (
        <div className={`nav-bar navbar-${mode} bg-${mode} text-${mode === 'dark' ? 'light' : 'dark'}`}>
            <div className="row align-items-center">
                <div className="col-lg-2">
                    <div className="logo">
                        <img src={logo} alt="Logo" />
                    </div>
                </div>
                <div className="col-lg-6">
                    <div className={`variety`}>
                        {/* Set the link color based on dark or light mode */}
                        <Link to="/" className={`nav-link text-${mode === 'dark' ? 'light' : 'dark'}`}>HOME</Link>
                        <Link to="/newarrival" className={`nav-link text-${mode === 'dark' ? 'light' : 'dark'}`}>NEW ARRIVALS</Link>
                        <Link to="/topwear" className={`nav-link text-${mode === 'dark' ? 'light' : 'dark'}`}>TOP WEAR</Link>
                        <Link to="/bottomwear" className={`nav-link text-${mode === 'dark' ? 'light' : 'dark'}`}>BOTTOM WEAR</Link>  
                    </div>
                </div>
                <div className="col-lg-4 d-flex">
                    <div className={`icon text-${mode === 'dark' ? 'light' : 'dark'}`}>
                        <div id="overlay" onClick="off()"></div>
                        <Link to="/login" className={`px-3 nav-link text-${mode === 'dark' ? 'light' : 'dark'}`} onClick="on()" >
                            <i className="bi bi-person-circle"></i><span>Login</span>
                        </Link>
                        <p>Cart</p><i className="bi bi-cart2"></i>
                    </div>
                    <div className="form-check form-switch">
                        <input 
                            className="form-check-input" 
                            onClick={toggleMode} 
                            type="checkbox" 
                            role="switch" 
                            id="flexSwitchCheckDefault" 
                        />
                        <label className="form-check-label" htmlFor="flexSwitchCheckDefault">
                            Enable dark mode
                        </label>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Navbar;
