import { useState } from "react";
import banner from "../Assests/Images/banner.jpg";
import bottom1 from "../Assests/Images/bottom wear-1.jpg";
import bottom2 from "../Assests/Images/bottom wear-2.webp";
import bottom3 from "../Assests/Images/bottom wear-3.webp";
import bottom4 from "../Assests/Images/bottom wear-4.webp";
import bottom5 from "../Assests/Images/bottom wear-5.avif";
import bottom6 from "../Assests/Images/bottom wear-6.webp";
import bottom7 from "../Assests/Images/bottom wear-7.webp";
import Footer from "./Footer";
import { Link } from "react-router-dom";
import { productdata } from "../Utils/data2";

const Bottomwear = () => {
  const [change, setchange] = useState("col-lg-4");
  const divchnage = (value) => {
    setchange(value);
  };

  return (
    <div className="px-5">
      <div className="head">
        <img src={banner} />
        <div className="abo">
          <h1>Bottom Wear</h1>
        </div>
      </div>
      <div className="dropdown d-flex justify-content-between">
        <div>
          <button
            className="btn btn-secondary dropdown-toggle"
            type="button"
            data-bs-toggle="dropdown"
            aria-expanded="false"
          >
            Sort by
          </button>
          <ul className="dropdown-menu">
            <li>
              <a class="dropdown-item" href="#">
                Price low to high
              </a>
            </li>
            <li>
              <a class="dropdown-item" href="#">
                Price high to low
              </a>
            </li>
            <li>
              <a class="dropdown-item" href="#">
                Arrange A-Z
              </a>
            </li>
          </ul>
        </div>
        <div className="butt">
          <button
            className="mx-1 p-2"
            value={`col-lg-6`}
            onClick={(e) => divchnage(e.target.value)}
          >
            2
          </button>
          <button
            className="mx-1 p-2"
            value={`col-lg-4`}
            onClick={(e) => divchnage(e.target.value)}
          >
            3
          </button>
          <button
            className="mx-1 p-2"
            value={`col-lg-3`}
            onClick={(e) => divchnage(e.target.value)}
          >
            4
          </button>
        </div>
      </div>

      <div className="toppro row justify-content-evenly">
        {productdata.map((item, index) => {
          return (
            <Link to={`/buybottom/${item.id}`} className={change} key={index}>
              <div className="top w-100" data-aos="zoom-in">
                <img src={item.img1} alt="img" />

                <div className="top-over">
                  <div className="top-over-butt">
                    <button>Add to cart</button>
                  </div>
                  <img src={item.img2} />
                </div>
              </div>
              <div className="price">
                <p>Rs.{item.price}</p>
              </div>
              <div className="det d-flex">
                <div className=" size">
                  <button className="mx-1">M</button>
                  <button className="mx-1">L</button>
                  <button className="mx-1">XL</button>
                  <button className="mx-1">XXL</button>
                </div>
              </div>
            </Link>
          );
        })}
      </div>

      <Footer />
    </div>
  );
};

export default Bottomwear;
