import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { productdata } from "../Utils/data2";
import e from "cors";
import useDarkMode from "../Utils/Common";

const Buybottom = () =>{
  const [mode, toggleMode] = useDarkMode();
    const [img,setImg] =useState();
    const handlechange =(e)=>{ 
      setImg("newImg");
    }
    const handleOnChange=(e)=>{
      setImg(e.target.value)
    }

    const { productid } = useParams();
    const [data,setData] = useState([])


    useEffect(() =>{
        const product = productdata.filter((item) => item.id == productid);
        setData(product)

    },[productid]);


    return(
        <>
          {data.map((item) =>{
          return (
           <div className="buy px-5">
             <div className="info row d-flex">
              <div className="col-lg-2">
                <div className="subimg">
                  <img src={item.img2} onClick={handlechange} />
                </div>
                <div className="subimg">
                  <img src={item.img1} />
                </div>
                <div className="subimg">
                  <img src={item.img2} />
                </div>
              </div>
              <div className="size col-lg-5">
                <h1>{img}</h1>
                <img src={item.img1}  alt="" />
              </div>
              <div className=" detail col-lg-4">
                <h4>{item.name}</h4>
                <p><i class="bi bi-currency-rupee"></i>{item.price}</p>
                <p>color</p>
                <div className="py-3">
                <div className="color"></div>
                </div>
                <button className="mx-1 p-1">M</button>
                <button className="mx-1 p-1">L</button>
                <button className="mx-1 p-1">XL</button>
                <button className="mx-1 p-1">XXL</button>
              </div>
             </div>
          </div>
          )
          })}
        </>
    );
}

export default Buybottom;