import React, { useEffect, useState } from "react";
import { data2 } from "../Utils/data3";
import { useParams } from "react-router-dom";
import useDarkMode from "../Utils/Common";

const BuyTop = () => {
  const [mode, toggleMode] = useDarkMode();
  const { productid } = useParams();
  const [data , setData] = useState([])
  
  useEffect(() => {
    const product = data2.filter((item) => item.id == productid);

    setData(product)
  }, [productid]);

  return (
    <>
       {data.map((item) =>{
        return (
            <div className="price buy px-5">
              <div className="info row d-flex">
                <div className="col-lg-2">
                    <div className="subimg">
                      <img src={item.img1} />
                    </div>
                    <div className="subimg">
                      <img src={item.img1} />
                    </div>
                    <div className="subimg">
                      <img src={item.img1} />
                    </div>
                  </div>
                  <div className="size col-lg-5">
                    <img src={item.img1} alt="" />
                  </div>
                  <div className="price detail col-lg-4">
                    <p>{item.name}</p>
                    <p><i className="price bi bi-currency-rupee"></i>{item.price}</p>
                    <button className="mx-1 p-1">M</button>
                    <button className="mx-1 p-1">L</button>
                    <button className="mx-1 p-1">XL</button>
                    <button className="mx-1 p-1">XXL</button>
                  </div>
              </div>
          </div>
        )
       })}
    
    </>
  );
};

export default BuyTop;
