import jean from '../Assests/Images/curly-haired-male-model-with-denim-jacket-ripped-jeans-w-high-fashion-forward-boutique-style_1020495-196583.avif';
import pho from '../Assests/Images/dsc08322_orig.jpg';
import phot from '../Assests/Images/dsc08617_orig.jpg';

const Products = () => {
  return (
    <div className="pro py-5" data-aos="fade-up">
      <div className="product-container">
       
        <div className="product-item">
          <img src={jean} alt="Denim Fashion" className="product-image" />
          <div className="overlay">
            <i className="bi bi-heart" aria-hidden="true"></i>
          </div>
        </div>

        <div className="product-item">
          <img src={pho} alt="Urban Style" className="product-image" />
          <div className="overlay">
            <i className="bi bi-heart" aria-hidden="true"></i>
          </div>
        </div>

        
        <div className="product-item">
          <img src={phot} alt="Street Style" className="product-image" />
          <div className="overlay">
            <i className="bi bi-heart" aria-hidden="true"></i>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Products;
