import React, { useState } from "react";
import ReactDOM from "react-dom";
import * as Components from "./Component";
import "../Assests/css/styles.css";
import axios from 'axios';
import md5 from "js-md5";


function LoginPage() {
  const [signIn, toggle] = React.useState(true);
  const [value,setValue] =useState({
    name:"",
    email:"",
    password:"",
  });

  const handleSubmit = (e) =>{
    e.preventDefault();

    const pass = md5(value.password)

    const data ={
      name: value.name,
      email: value.email,
      password: pass,
    }

    axios.post(`http://localhost:8081/addsign_in`,data)
    .then((res) =>{
      console.log(res.data)
    })
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setValue((prev) => ({
      ...prev,
      [name]: value,
    }));
  };
 


  return (
    <div className="login-main">
    
    <Components.Container>
      <Components.SignUpContainer signingIn={signIn}>
        <Components.Form onSubmit={handleSubmit}>
          <Components.Title>Create Account</Components.Title>
          <Components.Input type="text" onChange={handleChange} name="name" placeholder="Name" />
          <Components.Input type="email" onChange={handleChange} name="email" placeholder="Email" />
          <Components.Input type="password" onChange={handleChange} name="password" placeholder="Password" />
          <Components.Button>Sign Up</Components.Button>
        </Components.Form>
      </Components.SignUpContainer>
      <Components.SignInContainer signingIn={signIn}>
        <Components.Form>
          <Components.Title>Sign in</Components.Title>
          <Components.Input type="email" placeholder="Email" />
          <Components.Input type="password" placeholder="Password" />
          <Components.Anchor href="#">Forgot your password?</Components.Anchor>
          <Components.Button>Sign In</Components.Button>
        </Components.Form>
      </Components.SignInContainer>
      <Components.OverlayContainer signingIn={signIn}>
        <Components.Overlay signingIn={signIn}>
          <Components.LeftOverlayPanel signingIn={signIn}>
            <Components.Title>Welcome Back!</Components.Title>
            <Components.Paragraph>
              To keep connected with us please login with your personal info
            </Components.Paragraph>
            <Components.GhostButton onClick={() => toggle(true)}>
              Sign In
            </Components.GhostButton>
          </Components.LeftOverlayPanel>
          <Components.RightOverlayPanel signingIn={signIn}>
            <Components.Title>Hello, Friend!</Components.Title>
            <Components.Paragraph>
              Enter your personal details and start journey with us
            </Components.Paragraph>
            <Components.GhostButton onClick={() => toggle(false)}>
              Sign Up
            </Components.GhostButton>
          </Components.RightOverlayPanel>
        </Components.Overlay>
      </Components.OverlayContainer>
    </Components.Container>
    </div>
  );
}

export default LoginPage;
