import banner from '../Assests/Images/male-models-noah.jpg';
import banner1 from '../Assests/Images/banner-2.webp';
import banner2 from '../Assests/Images/banner-3.jpg';
import Products from './Products';

const Banner = (props) => {
  return (
    <>
      <div className={`banner py-5 data-aos="fade-in" text-${props.mode==='light'?'dark':'light'}`}>
        <div data-aos="fade-down">
           <div className="name" data-aos="fade-up" data-aos-duration="1000" data-aos-delay="100">
              <h1><span className="letter">C</span></h1>
              <h1><span className="letter">L</span></h1>
              <h1><span className="letter">O</span></h1>
              <h1><span className="letter">T</span></h1>
              <h1><span className="letter">H</span></h1>
              <h1><span className="letter">I</span></h1>
              <h1><span className="letter">N</span></h1>
              <h1><span className="letter">G</span></h1>
           </div>
          <div id="carouselExampleAutoplaying" className="carousel slide" data-bs-ride="carousel">
            <div className="carousel-inner">
              <div className="carousel-item active">
                <div className="carousel-overlay">
                  <img src={banner} className="d-block w-100" alt="Banner 1" />
                  <div className="overlay-content">
                    <h2>Fashion at its Best</h2>
                    <p>Explore the latest collection</p>
                  </div>
                </div>
              </div>
              <div className="carousel-item">
                <div className="carousel-overlay">
                  <img src={banner1} className="d-block w-100" alt="Banner 2" />
                  <div className="overlay-content">
                    <h2>Trendy Styles</h2>
                    <p>Shop the new arrivals</p>
                  </div>
                </div>
              </div>
              <div className="carousel-item">
                <div className="carousel-overlay">
                  <img src={banner2} className="d-block w-100" alt="Banner 3" />
                  <div className="overlay-content">
                    <h2>Summer Collection</h2>
                    <p>Bright, bold, and beautiful</p>
                  </div>
                </div>
              </div>
            </div>
            <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleAutoplaying" data-bs-slide="prev">
              <span className="carousel-control-prev-icon" aria-hidden="true"></span>
              <span className="visually-hidden">Previous</span>
            </button>
            <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleAutoplaying" data-bs-slide="next">
              <span className="carousel-control-next-icon" aria-hidden="true"></span>
              <span className="visually-hidden">Next</span>
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Banner;
