import { useState, useEffect } from 'react';

const useDarkMode = () => {
  const [mode, setMode] = useState('light');

  // Effect to apply the background color and text decoration when mode changes
  useEffect(() => {
    if (mode === 'dark') {
      document.body.style.backgroundColor = 'black';
      document.body.style.color = 'white'; // Update text color for better contrast
      document.body.style.textDecorationColor = 'white';
         // Apply dark mode styles to elements with class 'sec1'
        document.querySelectorAll('.sec1').forEach((element) => {
            element.style.color = '#fff';
        });
        document.querySelectorAll('.price').forEach((element) => {
            element.style.color = '#fff';
        });
    } else {
      document.body.style.backgroundColor = 'white';
      document.body.style.color = 'black';
      document.body.style.textDecorationColor = 'black';

       // Optionally, reset styles for elements with class 'sec1'
        document.querySelectorAll('.sec1').forEach((element) => {
            element.style.color = 'black';
        });
        document.querySelectorAll('.price').forEach((element) => {
            element.style.color = 'black';
        });
    }
  }, [mode]); // This effect runs when 'mode' changes

  // Function to toggle the mode
  const toggleMode = () => {
    setMode((prevMode) => (prevMode === 'light' ? 'dark' : 'light'));
  };

  return [mode, toggleMode];
};

export default useDarkMode;
