import Navbar from "../Layout/Navbar";
import new1 from "../Assests/Images/148a8bf21abeb27a79780b558770fbe1.jpg";
import new2 from "../Assests/Images/b9209e2c03ef7da44b088e0791ffd011.jpg";
import new3 from "../Assests/Images/blazer.jpg";
import banner from "../Assests/Images/banner.jpg";
import new4 from "../Assests/Images/new-1.jpg";
import new5 from "../Assests/Images/new-3.jpg";
import new6 from "../Assests/Images/blazer.jpg";
import new7 from "../Assests/Images/new-2.webp";
import { useEffect, useState } from "react";
import Footer from "./Footer";
import { product } from "../Utils/data";
import { faker } from '@faker-js/faker';

const Newarrival = () => {
  const [change, setchange] = useState("col-lg-4");
  

  const [fakeData, setFakeData] = useState([]);

  const generateFakeData = () => {
    const data = [];
    for (let i = 0; i < 10; i++) {
      data.push({
        id: i + 1,
        name: faker.commerce.productName() ,
        product1: faker.image.urlPicsumPhotos({ blur: 4 }),
        product2: faker.image.urlPicsumPhotos({ blur: 4, grayscale: true }),
        price : faker.commerce.price({ min: 100 })
      });
    }
    setFakeData(data);
  };


  useEffect(() => {
    generateFakeData();
  }, []);

  const divchnage = (value) => {
    setchange(value);
  };

  return (
    <div className="new px-5">
      <div className="head">
        <img src={banner} />
        <div className="abou">
          <h1>NEW ARRIVAL</h1>
        </div>
      </div>
      <div className="dropdown d-flex justify-content-between">
        <div>
          <button
            className="btn btn-secondary dropdown-toggle"
            type="button"
            data-bs-toggle="dropdown"
            aria-expanded="false"
          >
            Sort by
          </button>
          <ul className="dropdown-menu">
            <li>
              <a class="dropdown-item" href="#">
                Price low to high
              </a>
            </li>
            <li>
              <a class="dropdown-item" href="#">
                Price high to low
              </a>
            </li>
            <li>
              <a class="dropdown-item" href="#">
                Arrange A-Z
              </a>
            </li>
          </ul>
        </div>
        <div className="butt">
          <button
            className="mx-1 p-2"
            value={`col-lg-6`}
            onClick={(e) => divchnage(e.target.value)}
          >
            2
          </button>
          <button
            className="mx-1 p-2"
            value={`col-lg-4`}
            onClick={(e) => divchnage(e.target.value)}
          >
            3
          </button>
          <button
            className="mx-1 p-2"
            value={`col-lg-3`}
            onClick={(e) => divchnage(e.target.value)}
          >
            4
          </button>
        </div>
      </div>

      <div className="arriv row" data-aos="zoom-in">

        {fakeData.map((item) =>{
          return (
            <div className={change}>
            <div className="top w-100" data-aos="zoom-in">
              <img src={item.product1} alt="img" loading="lazy" />
              <div className="top-over">
                <div className="top-over-butt">
                  <button>Add to cart</button>
                </div>
                <img src={item.product2}  loading="lazy"/>
              </div>
            </div>
            <div className="price">
              <p>Rs.{item.price}</p>
            </div>
            <div>
              <p>{item.name}</p>
              </div>
            <div className="det d-flex">
              <div className=" size">
                <button className="mx-1">M</button>
                <button className="mx-1">L</button>
                <button className="mx-1">XL</button>
                <button className="mx-1">XXL</button>
              </div>
            </div>
          </div>
          )
        })}
  
        
        {/* <div className={change}>
          <div className="top w-100" data-aos="zoom-in">
            <img src={new1} alt="img" />

            <div className="top-over">
              <div className="top-over-butt">
                <button>Add to cart</button>
              </div>
              <img src={new2} />
            </div>
          </div>
          <div className="price">
            <p>Rs.999.00</p>
          </div>
          <div className="det d-flex">
            <div className=" size">
              <button className="mx-1">M</button>
              <button className="mx-1">L</button>
              <button className="mx-1">XL</button>
              <button className="mx-1">XXL</button>
            </div>
          </div>
        </div>
        <div className={change}>
          <div className="top w-100" data-aos="zoom-in">
            <img src={new7} alt="img" />

            <div className="top-over">
              <div className="top-over-butt">
                <button>Add to cart</button>
              </div>
              <img src={new6} />
            </div>
          </div>
          <div className="price">
            <p>Rs.999.00</p>
          </div>
          <div className="det d-flex">
            <div className=" size">
              <button className="mx-1">M</button>
              <button className="mx-1">L</button>
              <button className="mx-1">XL</button>
              <button className="mx-1">XXL</button>
            </div>
          </div>
        </div> */}


      </div>
      <Footer />
    </div>
  );
};

export default Newarrival;
